.footer{
    width: 100%;
    background: linear-gradient(0deg, rgb(202, 202, 202), rgba(255,255,255,1));
    height: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: x-small;
}
.footer-icons{
    padding: 0.3rem;
}
.footer-icons svg{
    font-size: x-large;
    cursor: pointer;
    transition: 0.15s ease-in-out;
}
.footer-icons svg:hover{
    color: #64ce4e;
}