.popular-card-row{
    
    position: relative;
    margin:  5%;  
    width: 92%;
    overflow: hidden;
    border-radius: 1.5rem;
    margin-top: 0;
    
    /* padding-left: 2rem; */
}
.popular-card{
    position: relative;
    height: 13rem;
    width: 100%;
}
.large{
     
        min-height:18rem;
        min-width: 100%;
        
}
.popular-card img{
   
    top: 0;
    border-radius: 1.5rem;
   height: 100%;
   width: 100%;
}
.popular-card p{
    font-weight: 300;
    position: absolute;
    bottom: 10%;
    left: 50%;
    transform: translate(-50%,0);
    color: white;
    z-index: 4;
    transition: 0.3s ease-in-out;
}

.gradient{
    position: absolute;
    height: 100%;
    width: 100%;
  top: 0;
  border-radius: 1.5rem;
    z-index:3;

width:100%;
height:100%;
background:linear-gradient(180deg,transparent,rgba(0,0,0,0), rgba(0,0,0,.5))

}
.gradient-row{
    position: absolute;
    height: 100%;
    width: 10%;
  top: 0;
  border-radius: 1.5rem;
    z-index:5;


background:linear-gradient(-90deg,transparent,rgba(0,0,0,0), rgba(0,0,0,.7))

}
.gradient-row-end{
    position: absolute;
    height: 100%;
    width: 10%;
  top: 0;
  right: 0;
  border-radius: 1.5rem;
    z-index:5;


background:linear-gradient(90deg,transparent,rgba(0,0,0,0), rgba(0,0,0,.7))

}
.div-title{
   font-weight: bolder;
    font-size: larger;
    margin-left: 4rem;
    margin-bottom: 0rem;
    padding: 0.3rem;
    
}