.cuisine-grid{
    position: relative;

    padding: 5rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(17em,1fr));
    grid-gap: 3rem;
}
.cuisine-card img{
    width: 100%;
    border-radius: 2rem;
}
.cuisine-card h4{
    text-align: center;
    padding: 1rem;
    text-transform: capitalize;
}
.head{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cuisine-name{
   
    border-bottom: solid 1px rgb(210, 210, 210);
    font-size: x-large;
   text-transform: capitalize;
    padding: 0 5rem ;   
    margin-bottom: -5rem;
    color: rgb(157, 157, 157);
}
