@import url('https://fonts.googleapis.com/css2?family=Berkshire+Swash&display=swap');

.nav-bar{
    
    width: 100%;
    height: auto;
    position: fixed;
    top: 0;

    padding: 0.5rem;
    display: flex;
    z-index: 10;
    background:linear-gradient(0deg,transparent,rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 1))

   
}
/* #581e8d */
.logo{
    font-family: 'Berkshire Swash', cursive;
    padding: 0.2rem 1rem;
    cursor: pointer;
    width: auto;
    color: rgb(31, 185, 0);
    transition: 0.3s ease-in-out;
    
}

.logo:hover{
    color: rgb(0, 0, 0);
}
span{
    
    transition: 0.02s ease-in-out;
}
span:hover{
    color: rgb(31, 185, 0);
    transform:translateY(-10px);
}

