.main{
  padding: 0 5rem;
}
.recipe-container{
    display: flex;
   justify-content: center;
    width: 100%;
    height: 100vh;
}
.recipe-info{
    max-width: 50%;
    padding: 3rem;
}

.recipe-info img{
    padding: 1rem 0;
    width: 100%;
    border-radius: 0.3rem;
}
.recipe-info h2{
    display: flex;
    font-size: large;
   width: auto;
    border-bottom: 1px solid rgb(185, 185, 185);
}
.summary{
    padding: 1rem 0;
    text-align: justify;
}
small{
    font-weight: bold;
    text-decoration: underline;
    
}
.recipe-details{
    padding: 3rem;
    margin-top: 2rem;
    width: 50%;
}
.recipe-details button{
    border-radius: 0;
    font-family: inherit;
    font-weight: bold;
    background: white ;
    border: 2px solid #1fb900;
    outline: none;
    padding: 0.7rem 1.4rem;
    margin: 0.5rem;
    transition: 0.2s ease-in-out;
}
.activeTab{
    background-color: #1fb900 !important;
    color: white;
}

.instructions{
    padding: 3rem 0;
    text-align: justify;
}
.recipe-details h3{
    text-align: center;
    color: #1fb900;
}