.cuisines{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cuisines h4{
   
    transform: scale(0.8);
    font-size: 2px;
}
.cuisine{
    font-weight: 300;
    color: white;
    border-radius: 50%;
    max-width: 3.6rem;
    min-width: 3.6rem;
    max-height: 3.6rem;
    min-height: 3.6rem;
    background: rgb(47, 47, 47);
    margin: 1rem;
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: 0.3s ease-out;
}

.cuisine:hover{
    transform: scale(1.05);
    color: black;
    background-color: white;
    /* transform: rotate(360deg); */
    transform: rotate(360deg);
    box-shadow:0px 0px 10px 0px rgba(0,0,0,0.2);

}
.active{
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 4.3rem;
    max-height: 4.3rem;
    border-radius: 50%;
    /* background: rgb(207, 207, 207); */
    
    }
    .active .cuisine{
        background-color: white;
        transform: scale(1.05);
        color: black;
        box-shadow:0px 0px 10px 0px rgba(0,0,0,0.2);
    }
a{
    text-decoration: none;
}