.search-div{
    margin-top: 3rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
form{
    position: relative;
    display: flex;
    align-items: center;
    justify-self: center;
    background:linear-gradient(35deg, #494949,#313131);
    margin: 3rem;
    padding: 0.7rem;
    border-radius: 1rem;
    width: 50%;
}
input{
    margin-left: 20px;
    font-size: 1rem;
    width: 100%;
    height: 100%;
    background:linear-gradient(35deg, #494949,#313131);
    color: white;
    border: none;
    outline: none;
}
.search-div svg{
position: absolute;
color: white;
left: 10px;
cursor: pointer;
}